<template>
<div class="room" :id="['room_' + roomKey]" >
  <Jump :roomWidth="roomWidth" v-if="type===0" ref='Room0'></Jump>
  <Dash :roomWidth="roomWidth*1.2" v-if="type===1" ref='Room1'></Dash>
  <Slide :roomWidth="roomWidth" v-if="type===2" ref='Room2'></Slide>
  <Wait :roomWidth="roomWidth" v-if="type===3" ref='Room3'></Wait>
</div>
</template>

<script>
import Dash from './rooms/dash.vue';
import Slide from './rooms/slide.vue';
import Jump from './rooms/jump.vue';
import Wait from './rooms/wait.vue';
export default {
  name: 'Room',
  components: {
     'Dash': Dash,
        'Slide': Slide,
           'Jump': Jump,
              'Wait': Wait,
  },
  props: {
    id: 'room',
    type: Number,
    roomKey: Number,
    roomWidth:Number
  },
  data() {
    return {
    }
  },
  methods: {
    triggerAnim: function(type) {
      this.$refs['Room'+type].trigger();
      //console.log('triggerAnim');
    },
    init: function() {

    },
  },
  mounted() {
    // this.$emit('backgroundCreated')
  }
}
</script>
<style lang="scss" scoped>

	$roomWidth: 400px;
  .room {

		// width: $roomWidth;
		// height: $roomWidth;
		// background: #EEE;
		// border: 1px solid #000;
		position: absolute;
		p {
			font-size: 30px;
			text-align: center;
			width: 100%;
		}
	}
</style>



