<template>
<div id="dash" :style="{marginTop:roomWidth/-4+'px',marginLeft:roomWidth/-4+'px',width:roomWidth/2+'px',height:roomWidth/2+'px'}">
  <img id="down" style="position:absolute;bottom:0;width:100%;z-index:-1;" :style="{bottom:roomWidth/-4+'px',marginBottom: '-10%'}" src="../../assets/traps/dash/dashdown.png" alt="">
<img style="position:absolute;bottom:0;width:100%;z-index:-1;" :style="{bottom:(roomWidth/-4)-10+'px'}" src="../../assets/traps/dash/dashforground.png" alt="">
<img id="top" style="position:absolute;bottom:0;width:100%;z-index:-1;" :style="{top:(roomWidth/-4)+'px',marginTop: '-100%'}" src="../../assets/traps/dash/dashtop.png" alt="">

</div>
</template>

<script>
import Velocity from 'velocity-animate';
export default {
  props: {
    roomWidth:Number
  },
  data() {
    return {
    }
  },
  methods: {
    trigger() {
      $("#down").velocity({ marginBottom: '50%' },
      {
        duration: 200,
        easing:'linear',
        delay: 800,
      }).velocity({ marginBottom: '-10%' },
      {
        duration: 200,
        easing:'linear',
        delay: 800,
      });
      $("#top").velocity({ marginTop: '0%' },
      {
        duration: 200,
        delay: 800,
        easing:'linear',
      }).velocity({ marginTop: '-100%' },
      {
        duration: 200,
        delay: 800,
        easing:'linear',
      });
  },
    init: function() {},
  },
  mounted() {
    // this.$emit('backgroundCreated')
  }
}
</script>
<style>
  #dash{
    position: absolute;
    top: 50%;
    left: 50%;
  }
</style>



